import React from 'react';
import { Table, TableRow, TableHead, TableBody, Paper, TableCell, Typography } from '@material-ui/core';

import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';

import blue from '@material-ui/core/colors/blue';

import moment from 'moment'

const styles = theme => ({
    root: {
        width: '100%',
        marginTop: theme.spacing.unit,
        overflowX: 'auto',
    },
    table: {
        width: "100%",
        minWidth: 0,
        colWidth: '2rem',
        tableLayout: 'fixed',
        borderWidth: 1,
        borderColor: 'black',
        borderStyle: 'solid'
    },
    tideLevelLow: {
        backgroundColor: blue[100], //theme.palette.grey['400'], //theme.pallete.grey['400'], //'lightGray',
        borderWidth: 1,
        borderColor: 'black',
        borderStyle: 'solid'
    },
    tideLevelHigh: {
        backgroundColor: blue[200], //theme.palette.grey['600'], //theme.pallete, //'lightBlue',
        borderWidth: 1,
        borderColor: 'black',
        borderStyle: 'solid',
        textColor: 'yellow'
    },
    columnHeader: {
        backgroundColor: 'orange',
        borderWidth: 1,
        borderColor: 'black',
        borderStyle: 'solid'
    },
    todayColumnHeader: {
        backgroundColor: 'darkOrange',
        borderWidth: 2,
        borderColor: 'black',
        borderStyle: 'solid'
    },
    todayColumnTideLevelLow: {
        backgroundColor: blue[300],
        borderWidth: 2,
        borderColor: 'black',
        borderStyle: 'solid'
    },
    todayColumnTideLevelHigh: {
        backgroundColor: blue[500],
        borderWidth: 2,
        borderColor: 'black',
        borderStyle: 'solid'
    }
});

const TideTimesGrid = (props) => {

    // console.log(theme.palette);

    const { classes } = props;

    const headers = [];

    if (!props['data']) {
        return (<Typography variant="h6">No Data for Site</Typography>)
    }
    Object.keys(props['data']).forEach((dateStr, index) => {
        var theDate = moment(dateStr, "YYYYMMDD");
        if (theDate.isSame(moment(), 'day')) {
            headers.push("Today");
        } else if (theDate.isSame(moment().subtract(1, 'day'), 'day')) {
            headers.push("Yesterday");
        } else if (theDate.isSame(moment().add(1, 'day'), 'day')) {
            headers.push("Tomorrow");
        } else {
            headers.push(dateStr.slice(6) + "/" + dateStr.slice(4, 6) + "/" + dateStr.slice(2, 4));
        }

    });

    const transformedData = [[], [], [], []];
    for (let x in [0, 1, 2, 3]) {
        Object.keys(props['data']).forEach((dateStr) => {
            const timesData = props['data'][dateStr];
            if (timesData) {
                timesData[x].isToday = (moment(dateStr, "YYYYMMDD").isSame(moment(), 'day'));
                transformedData[x].push(timesData[x]);
            } else {
                transformedData[x].push({});
            }
        });
    }

    return (

        <Paper className={classes.root}>
            <Table className={classes.table}>
                <TableHead className={classes.columnHeader}>
                    <TableRow>
                        { // Header
                            headers.map((header, index) => {
                                return (
                                    <TableCell key={index}
                                        className={(index === 1) ? classes.todayColumnHeader : classes.columnHeader}
                                    >
                                        <Typography variant={ header === 'Today' ? 'h6' : 'body1'}>
                                            {header}
                                        </Typography>
                                    </TableCell>
                                );
                            })
                        }
                    </TableRow>
                </TableHead>
                <TableBody>
                    { // Each row
                        transformedData.map((data, indexOuter) => {
                            return (
                                <TableRow key={indexOuter}>
                                    {
                                        data.map((timeData, indexInner) => {
                                            if (timeData && timeData.time) {
                                                return (
                                                    <TableCell
                                                        className={
                                                            [
                                                                timeData.isToday
                                                                ?
                                                                    timeData.isLow ? classes.todayColumnTideLevelLow : classes.todayColumnTideLevelHigh
                                                                :
                                                                    timeData.isLow ? classes.tideLevelLow : classes.tideLevelHigh
                                                            ].join(",")
                                                        }
                                                        key={indexOuter + indexInner}
                                                        color="primary"
                                                    >
                                                        <Typography variant="h6" >{timeData.time.trim()}</Typography>
                                                    </TableCell>
                                                );
                                            } else {
                                                return (
                                                    <TableCell className={classes.tideLevelLow} key={indexOuter + indexInner}>
                                                        <Typography variant="body1">{"----".trim()}</Typography>
                                                    </TableCell>
                                                )
                                            }
                                        })
                                    }
                                </TableRow>
                            );
                        })
                    }
                </TableBody>
            </Table>
        </Paper>

    )

}

TideTimesGrid.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(TideTimesGrid);
