import React, { Component } from 'react';
import axios from 'axios';
import dateFormat from 'dateformat';
import { FormattedMessage } from 'react-intl';
import Typography from '@material-ui/core/Typography';
import queryString from 'query-string';
import TideTimesLayout from './TideTimesLayout/TideTimesLayout'

import moment from 'moment'

function isEmpty(obj) {
    for (var key in obj) {
        if (obj.hasOwnProperty(key))
            return false;
    }
    return true;
}

class TideTimes extends Component {

    state = {
        loading: false,
        data: {}
    }

    constructor(props) {

        super(props);

        this.todayStr = moment().format('YYYYMMDD').toString();
        this.yesterdayStr = moment().subtract(1, 'day').format('YYYYMMDD').toString();
        this.tomorrowStr = moment().add(1, 'day').format('YYYYMMDD').toString();

    }

    componentDidMount() {
        const values = queryString.parse(this.props.location.search);
        console.log("TideTimes.componentDidMount() Site = " + values.siteId);
        this.loadData(values.siteId, [this.yesterdayStr, this.todayStr, this.tomorrowStr].join(","));
    }

    componentWillReceiveProps(newProps) {
        const values = queryString.parse("/" + newProps.location.search);
        var newSiteId = (values['siteId'] ? values['siteId'] : values["/?siteId"]);
        this.loadData(newSiteId, [this.yesterdayStr, this.todayStr, this.tomorrowStr].join(","));
    }

    setHighLowFlags(dataObj) {
        Object.keys(dataObj).forEach((siteId, siteIndex) => {
            Object.keys(dataObj[siteId]).forEach((date, dateIndex) => {
                var times = dataObj[siteId][date];
                var startLow = times[0]['height'] < times[1]['height'];
                times.forEach((times) => {
                    times['isLow'] = startLow;
                    startLow = !startLow;
                });
            });
        });
    }

    loadData = (siteIdCsv, dateStrCsv) => {
        siteIdCsv = siteIdCsv ? siteIdCsv : 'salcombe';
        dateStrCsv = dateStrCsv ? dateStrCsv : dateFormat(new Date(), "yyyymmdd");
        console.log("TideTimes.loadData() siteId = " + siteIdCsv + " For " + dateStrCsv);
        axios.get("https://opalescent-radiator.glitch.me?locationIds=" + siteIdCsv + "&dates=" + dateStrCsv, { crossdomain: true })
            .then(response => {
                const jsonFromServer = response.data;
                this.setHighLowFlags(response.data['data']);
                this.setState(jsonFromServer);
            })
            .catch(error => {
                console.error("error: ", error);
                this.setState({error: error});
            });
    }

    render() {

        const { loading, error, data } = this.state;

        if (loading) {
            return <Typography variant="h3"><FormattedMessage id="App.error.loading" /></Typography>
        }

        if (!data || isEmpty(data)) {
            return <Typography variant="h3"><FormattedMessage id="App.error.no_data_avail" /></Typography>
        }

        if (error) {
            return (
                <Typography variant="h4">
                    There was an error loading the repos.{" "}
                    <button onClick={this.loadData}>Try again</button>
                </Typography>
            );
        }

        return (<TideTimesLayout key={this.props.location.pathname + this.props.location.search} siteData={this.state.data} />)

    }

}

export default TideTimes;